import React, { memo, useEffect, useMemo, useState } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { Button, ModalR, SelectD } from '../common';
import clsg from './CustomGeneric.module.scss';
import cls from './CustomRoutes.module.scss';

import ModuleLists from '../SideMenuEntries/ModulesComp/ModuleLists';
import { add } from '../../assets/images/icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarDays, faThumbsUp, faTrash, faUserXmark } from '@fortawesome/free-solid-svg-icons';
import moment, { now } from 'moment';
import { store } from '../../reduxStore/store';
import { generateNewUUID } from '../../shared/utility';
import { processTaskAction, useProcessTaskContext } from '../../context/processTask.context';

import * as R from 'ramda';
import SelectAndMultiselect from '../SideMenuEntries/ProcessesComps/SelectAndMultiselect/SelectAndMultiselect';
import DatePicker from 'react-datepicker';
import { eachDayOfInterval, format, startOfWeek, endOfWeek, endOfMonth, startOfMonth } from "date-fns";

import ReactQuill from "react-quill"
import 'react-quill/dist/quill.snow.css'
import { Styles, Table } from '../common/UI/Table/Table';
import GoogleMaps from '../common/Mapping/GoogleMaps/GoogleMaps';

const CustomRoutes = (props) => {

    const { begunModule, allModules, companyUsersArray, goto, components, authenticatedUser, user, companyProcesses, template, component } = props;
    const { title, selectedComponent, setSelectedComponent, step } = props;
    const { companyData } = useSelector(state => state.flowReducer);
    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();
    const [selectedItems, setSelectedItems] = useState({});
    const [orderItems, setOrderItems] = useState([]);
    const [listItems, setListItems] = useState([]);
    const [showSecond, setShowSecond] = useState();
    const [showThird, setShowThird] = useState();
    const [loading, setLoading] = useState(false);
    const customer = component.selected_fields.find(sf => sf.data_type_name === 'Record' && sf.field_name === 'Customer');
    const items = components.filter(c => c.component_fields.filter(cf => cf.component_blueprint_id === customer?.options?.component_id).length > 0);
    const columns = useMemo(
        () => [
            {
                Header: 'View',
                columns: [
                    {
                        Header: 'View',
                        accessor: 'view',
                    },
                ],
            },
        ],
        []
    );
    // console.log(customer, listItems, processTaskContext)
    return <div className={clsg.accordionContainer}>
        <div style={{ display: 'flex', width: '100%' }}>
            <div style={{ width: '20%', padding: '20px 10px 0 10px' }}>
                Customers:
                <Styles>
                    <Table columns={columns} data={processTaskContext?.items.map(li => {
                        return {
                            id: li.id, view: <div style={{ border: '1px dashed gray', padding: '10px' }}>
                                {li.title}
                            </div>
                        }
                    })} onhandleDragEnd={() => { }} />
                </Styles>

                <div style={{ disabled: true, width: '250px', display: isMobile ? 'none' : 'flex', flexDirection: 'column', marginRight: '0px', marginTop: `${280 + (processTaskContext?.items?.length ?? 1) * 60}px`, fontSize: '18px' }}>
                    Filters:
                    {customer?.options?.filters?.length > 0 && (
                        <div className={cls.dropdownContainer}>
                            <SelectD
                                items={
                                    items
                                        ? R.uniqWith(R.eqProps('value'))(
                                            items.map(item => {
                                                const found = item.component_fields.find(sf => sf.field_name === customer?.options?.filters[0]);
                                                return { label: found?.value, value: found?.value, component: item };
                                            })
                                        )
                                        : []
                                }
                                onChange={value => {
                                    setOrderItems([]);
                                    setSelectedItems({ [customer?.options?.filters[0]]: value.value });
                                    setListItems([]);
                                    setShowThird(false);
                                    setShowSecond(false);
                                    setTimeout(() => setShowSecond(true), 10);
                                    setListItems(items?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0));
                                    setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: processTaskContext.currentStep?.component_blueprints ?? [] });
                                    setOrderItems(
                                        items
                                            ?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0)
                                            .map(lineItem => {
                                                let object = {};
                                                component.selected_fields.filter(fd => {
                                                    if (fd.data_type_name === 'Text' || fd.data_type_name === 'Number') {
                                                        object = { ...object, [fd.field_name]: lineItem.component_fields.find(li => li.field_name === fd.field_name)?.value ?? 0 };
                                                    }
                                                });
                                                return object;
                                            })
                                    );
                                }}
                                placeholder={customer?.options?.filters[0]}
                                moliclassname={cls.componentsSelectMenuOverlayListItem}
                                className={cls.currencyInput}
                                tmiClassName={cls.tmiClassName}
                            />
                        </div>
                    )}
                    <div style={{ height: '10px' }} />
                    {customer?.options?.filters?.length > 1 && !isMobile && showSecond && (
                        <div className={cls.dropdownContainer}>
                            <SelectD
                                items={
                                    selectedItems[customer?.options?.filters[0]]
                                        ? R.uniqWith(R.eqProps('value'))(
                                            items
                                                .map(item => {
                                                    const found = item.component_fields.find(sf => sf.field_name === customer?.options?.filters[1]);
                                                    return { label: found?.value, value: found?.value, component: item };
                                                })
                                                .filter(found => found.component.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]] && cf.field_name === customer?.options?.filters[0]).length > 0)
                                        )
                                        : []
                                }
                                onChange={value => {
                                    setShowThird(false);
                                    setTimeout(() => setShowThird(true), 10);
                                    setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: processTaskContext.currentStep?.component_blueprints ?? [] });
                                    setSelectedItems({ ...selectedItems, [customer?.options?.filters[1]]: value.value });
                                    setOrderItems(
                                        items
                                            ?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0)
                                            .filter(item => item.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]])?.length > 0)
                                            .map(lineItem => {
                                                let object = {};
                                                component.selected_fields.filter(fd => {
                                                    if (fd.data_type_name === 'Text' || fd.data_type_name === 'Number') {
                                                        object = { ...object, [fd.field_name]: lineItem.component_fields.find(li => li.field_name === fd.field_name)?.value ?? 0 };
                                                    }
                                                });
                                                return object;
                                            })
                                    );
                                    setListItems(items?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0).filter(item => item.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]])?.length > 0));
                                }}
                                placeholder={customer?.options?.filters[1]}
                                style={{ fontSize: '16px' }}
                                moliclassname={cls.componentsSelectMenuOverlayListItem}
                                tmiClassName={cls.tmiClassName}
                            />
                        </div>
                    )}
                    <div style={{ height: '10px' }} />
                    {customer?.options?.filters?.length > 2 && !isMobile && showThird && (
                        <div className={cls.dropdownContainer}>
                            <SelectD
                                items={
                                    selectedItems[customer?.options?.filters[1]]
                                        ? R.uniqWith(R.eqProps('value'))(
                                            listItems
                                                .map(item => {
                                                    const found = item.component_fields.find(sf => sf.field_name === customer?.options?.filters[2]);
                                                    return { label: found?.value, value: found?.value, component: item };
                                                })
                                                .filter(found => found.component.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[1]] && cf.field_name === customer?.options?.filters[1]).length > 0)
                                        )
                                        : []
                                }
                                onChange={value => {
                                    setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: processTaskContext.currentStep?.component_blueprints ?? [] });
                                    setSelectedItems({ ...selectedItems, [customer?.options?.filters[1]]: value.value });
                                    setOrderItems(
                                        items
                                            ?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0)
                                            .filter(item => item.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]])?.length > 0)
                                            .map(lineItem => {
                                                let object = {};
                                                component.selected_fields.filter(fd => {
                                                    if (fd.data_type_name === 'Text' || fd.data_type_name === 'Number') {
                                                        object = { ...object, [fd.field_name]: lineItem.component_fields.find(li => li.field_name === fd.field_name)?.value ?? 0 };
                                                    }
                                                });
                                                return object;
                                            })
                                    );
                                    setListItems(items?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0).filter(item => item.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]])?.length > 0));
                                }}
                                placeholder={customer?.options?.filters[2]}
                                style={{ fontSize: '16px' }}
                                moliclassname={cls.componentsSelectMenuOverlayListItem}
                                tmiClassName={cls.tmiClassName}
                            />
                        </div>
                    )}
                    
                    <ModuleLists
                        className={cls.itemsTable}
                        columns={['Name']}
                        modern={true}
                        //withColumns={false}
                        list={listItems?.map(li => {
                            return {
                                id: li.id, Name: <div style={{ padding: '10px' }}>
                                    {li.title}
                                </div>
                            }
                        })}

                    />

                </div>
            </div>
            <div style={{ width: '80%' }}>
                <GoogleMaps apikey={companyData?.misc_info?.map} search={false} />
            </div>
        </div>
    </div>;
}

const mapStateToProps = store => {
    const {
        authenticatedUser: { position },
        authenticatedUser,
    } = store.authReducer;
    const { user } = store.sessionReducer;

    const { begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, companyProcesses } = store.flowReducer;
    return {
        begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, authenticatedUser, user, companyProcesses
    };
};


export default connect(mapStateToProps)(CustomRoutes);

